import { Component, OnInit } from '@angular/core';

import {LogService} from '../../services/log.service';

import {Log} from '../../models/log';

import * as SockJS from 'sockjs-client';

import * as Stomp from 'stompjs';


@Component({
  selector: 'app-home',
  templateUrl: './home.component.html',
  styleUrls: ['./home.component.css']
})
export class HomeComponent implements OnInit {

	isEditingLog: boolean = false;
	id:number;
	title:string;
	body:string;
	author:string;
	date:Date;
	showForm:boolean = false;
	loading:boolean = true;
	 logs: Log[] = [];
	 
	 
	client:any = Stomp.over(new SockJS("/devlogger-socket"));
  constructor(public logService: LogService) {
	 var that = this;
	
	this.client.connect({},function(frame){
			that.client.subscribe("/topic/log",function(log){
				//that.logs.push(JSON.parse(log.body).content);
				that.pushLog(JSON.parse(log.body) as Log);
			});
		});

  }
	
	pushLog(log:Log){
		this.logs.push(log);
	}
  
  ngOnInit() {
	this.logService.getLogs().subscribe(logs =>{
		this.loading = false;
		console.log("working");
		this.logs = logs;
		
		
		
	});
	
	
	
	
  }
  
  
  
  
  
  
  addLog(title: string, log:string){
	this.logService.addLog({title,log} as Log).subscribe(log => {
		//this.logs.push(log);
	});
  }
  
  editLog(log){
		this.logService.updateLog(log).subscribe(eLog =>{
			for( let i=0;i<this.logs.length;i++){
				if(this.logs[i].id == eLog.id){
					this.logs[i] = eLog;
					break;
				}
			}
		});
	}
  
  
  deleteLog(id:number){
	if(this.isEditingLog && id === this.id){this.isEditingLog= false}
	this.logService.deleteLog(id).subscribe(()=>{
		for(let i=0;i<this.logs.length;i++){
			if(this.logs[i].id == id){
				this.logs.splice(i,1);
				break;
			}
		}
	});
  }
  
  onedit(log:Log){
	this.isEditingLog = true;
	if(this.showForm === false){this.toggleShowForm();}
	this.id =log.id;
	this.title = log.title;
	this.body = log.log;
	this.author = log.author;
	this.date = log.date;
	
  }
  
  onsubmit(){
	if(this.isEditingLog){
		const updLog= {
			id:this.id,
			title:this.title,
			log:this.body,
			author:this.author,
			date:this.date
		}
		this.editLog(updLog);
	}else{
		this.addLog(this.title,this.body);
	}
	
	this.clear();
  }
  
  clear(){
	this.isEditingLog = false;
	this.title ="";
	this.body="";
  }
  
  toggleShowForm(){
	this.showForm = !this.showForm;
  }
  
  

}
