import { Injectable } from '@angular/core';
import { HttpClient, HttpHeaders } from  '@angular/common/http';
import {Observable} from "rxjs/Observable"
import {Log} from '../models/log';

const httpOptions = {
	headers: new HttpHeaders({'content-type':"application/json"})
  }


@Injectable({
  providedIn: 'root'
})
export class LogService {
  
  url: string = "http://localhost:8080/api/logs";
  
  constructor(private http: HttpClient) { }
 
 
  getLogs(): Observable<Log[]>{
	return this.http.get<Log[]>(this.url);
  }
  
  addLog(log:Log): Observable<Log>{
	return this.http.post<Log>(this.url,log,httpOptions);
  }
  
  updateLog(log: Log): Observable<Log>{
	return this.http.put<Log>(this.url+"/"+log.id,log,httpOptions);
  }
  
  deleteLog(id: number): Observable<Log>{
	return this.http.delete<Log>(this.url +"/"+id, httpOptions);
  }
  
  
  
}
